.catalog {
  margin-bottom: 26px;
  padding-top: 27px;
  padding-bottom: 20px;
  border-bottom: 1px solid $color-very-light-grey;

  @media (min-width: $tablet-width) {
    margin: 0;
    padding-top: 50px;
    padding-bottom: 68px;
    border: none;
  }

  @media (min-width: $desktop-width) {
    padding-top: 54px;
    padding-bottom: 74px;
  }
}

.catalog__title {
  margin: 0;
  margin-bottom: 41px;
  font-weight: 400;
  font-size: 36px;
  line-height: 36px;
  position: relative;

  @media (min-width: $tablet-width) {
    font-size: 60px;
    line-height: 60px;
    margin-bottom: 117px;
  }

  @media (min-width: $desktop-width) {
    font-size: 60px;
    line-height: 60px;
    margin-bottom: 120px;
  }
}

.catalog__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: grid;
  grid-template-columns: 1fr;

  @media (min-width: $tablet-width) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 95px 9%;
  }

  @media (min-width: $desktop-width) {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 97px 80px;
  }
}

.catalog__item {
  padding: 20px 0 20px 0;
  margin-bottom: 5px;
  border-top: 1px solid $color-whisper;

  @media (min-width: $tablet-width) {
    background-image: linear-gradient(
      to bottom,
      transparent 45px,
      #f2f2f2 45px
    );
    padding: 0 11% 12% 12%;
    margin: 0;
    border: none;
  }

  @media (min-width: $desktop-width) {
    padding: 0 16% 17% 16%;
  }
}

.catalog__button {
  font-size: 16px;
  line-height: 1.25;
  padding: 11px;
  background-color: $color-white-smoke;
  color: #444444;
}

.catalog__more {
  padding: 77px 0 0 0;
  margin-bottom: 0;
  position: relative;
  border-top: 1px solid $color-white-smoke;

  @media (min-width: $tablet-width) {
    margin-top: 47px;
    background: none;
    padding: 50% 11% 11% 11%;
    background-color: $color-default-white;
    border: 2px solid $color-white-smoke;
  }

  @media (min-width: $desktop-width) {
    padding: 64% 15% 11% 14%;
  }

  &::before {
    position: absolute;
    content: "";
    background-image: url("../img/catalog/plus-mobile.svg");
    background-repeat: no-repeat;
    width: 30px;
    height: 30px;
    top: 25px;
    left: 50%;
    transform: translateX(-50%) translateZ(0);

    @media (min-width: $tablet-width) {
      background-image: url("../img/catalog/plus-tablet.svg");
      width: 60px;
      height: 60px;
      top: 50px;
    }
  }
}

.catalog__title-more {
  margin: 0;
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  color: $color-black;
  text-align: center;
  text-transform: uppercase;
  margin-bottom: 7px;
  position: relative;

  @media (min-width: $tablet-width) {
    font-size: 20px;
    line-height: 22px;
    width: 134px;
    margin: 0 auto 22px;
  }
}

.catalog__subtitle-more {
  margin: 0;
  font-family: $Arial;
  font-size: 12px;
  text-align: center;
  margin-bottom: 21px;

  @media (min-width: $tablet-width) {
    font-size: 16px;
    margin: 0 auto 85px;
    width: 170px;
  }
}
