@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Oswald";
  font-display: swap;
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Oswald";
  font-display: swap;
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100%;
  overflow-x: hidden;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.container {
  padding-left: 20px;
  padding-right: 20px;
  width: 100%;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .container {
    padding-left: 30px;
    padding-right: 30px;
    max-width: 100%;
  }
}
@media (min-width: 1024px) {
  .container {
    padding-left: 40px;
    padding-right: 40px;
    max-width: 1300px;
  }
}

.page__body {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  min-width: 320px;
  width: 100%;
  margin: 0 auto;
  position: relative;
  overflow-x: hidden;
}

.header {
  border-bottom: 1px solid #e6e6e6;
}
@media (min-width: 768px) {
  .header {
    border: none;
  }
}

.header__wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  padding-top: 12px;
  padding-bottom: 12px;
  position: relative;
}
@media (min-width: 768px) {
  .header__wrapper {
    padding-top: 24px;
    padding-bottom: 13px;
  }
}
@media (min-width: 1024px) {
  .header__wrapper {
    padding-top: 51px;
    padding-bottom: 13px;
    z-index: 1;
  }
}

.header__logo:hover {
  opacity: 0.8;
}
.header__logo:active {
  opacity: 0.6;
}

@media (min-width: 768px) {
  .header__logo-image {
    width: 174px;
    height: 52px;
  }
}
@media (min-width: 1024px) {
  .header__logo-image {
    width: 202px;
    height: 66px;
  }
}

.header__nav {
  display: none;
  width: 100%;
  margin-top: 13px;
}
@media (min-width: 768px) {
  .header__nav {
    width: auto;
    display: block;
    margin: 0;
  }
}

.header__nav--opened {
  display: block;
}

.header__nav-list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  text-align: center;
}
@media (min-width: 768px) {
  .header__nav-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
  }
}

.header__nav-no-js {
  display: block;
}

.header__nav-link {
  display: block;
  font-size: 20px;
  line-height: 20px;
  font-weight: 700;
  text-decoration: none;
  text-transform: uppercase;
  text-align: center;
  color: #000000;
  padding-top: 22px;
  padding-bottom: 24px;
  border-top: 1px solid #e6e6e6;
  margin-left: -20px;
  margin-right: -20px;
  position: relative;
}
@media (min-width: 768px) {
  .header__nav-link {
    margin: 0;
    padding: 16px 0 11px 0;
    font-size: 18px;
    font-weight: 400;
