    font-size: 20px;
    line-height: 30px;
    color: #ffffff;
    padding: 4px 0 8px 0;
  }
}
.header__nav-link:hover {
  opacity: 0.6;
}
.header__nav-link:active {
  opacity: 0.3;
}

@media (min-width: 768px) {
  .header__nav-item {
    margin-left: 34px;
  }
  .header__nav-item:last-child {
    margin-left: 38px;
  }
}
@media (min-width: 1024px) {
  .header__nav-item {
    margin-left: 37px;
  }
  .header__nav-item:last-child {
    margin-left: 35px;
  }
}

@media (min-width: 768px) {
  .header__nav-link--current::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    height: 2px;
    background-color: #68b738;
    width: 100%;
  }
}
@media (min-width: 1024px) {
  .header__nav-link--current::after {
    background-color: #ffffff;
  }
}

@media (min-width: 768px) {
  .header__nav-link-catalog--current::after {
    content: "";
    position: absolute;
    bottom: 0;
    right: 0;
    height: 1px;
    background-color: #68b738;
    width: 100%;
  }
}

.header__nav-link-catalog {
  color: #000000;
}

.header__burger {
  display: block;
  width: 24px;
  height: 19px;
  border: none;
  position: relative;
  background-color: #ffffff;
}
.header__burger:hover, .header__burger:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .header__burger {
    display: none;
  }
}

.header__burger--closed::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 24px;
  height: 1.5px;
  background-color: #000000;
  box-shadow: 0 7.5px 0 0 #000000, 0 15.5px 0 0 #000000;
}

.header__burger--opened::before, .header__burger--opened::after {
  content: "";
  position: absolute;
  left: 0;
  width: 24px;
  height: 1.5px;
  background-color: #000000;
}
.header__burger--opened::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.header__burger--opened::after {
  transform: rotate(-45deg);
}

